<template>
  <div class="main_navs">
    <Header></Header>
    <div class="container mb-4" v-if="is_response">
      <!--      <div class="title_ans">-->
      <!--        Тест на темперамент-->
      <!--      </div>-->
      <div class="ans_card">
        <div class="row">
          <div class="col-xl-8">

            <div class="ans_title">{{an_res.title}}</div>
            <div class="ans_desc">
              {{an_res.description}}
            </div>

            <div class="link_group">
              <div>
                <a @click="getOnetest()" class="link_main decoration" href="#">
                  <div>
                    Testga qaytish
                  </div>
                </a>
              </div>
              <div>
                <router-link class="link_main_calk decoration" to="/start-test">
                  <div>
                    Boshqa testga kirish
                  </div>
                </router-link>
              </div>
            </div>
          </div>
          <div class="col-xl-4">
            <img :src="an_res.image_url" width="100%" class="ans_img" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="container" v-if="!is_response">

      <div class="on_test_title">{{$store.state.testsId.title}}</div>
      <div class="on-test">
        <div class="one_test_progress">
          <div class="on-test-count">
            Вопросы {{ sliderValue }}/{{ $store.state.test_one.length }}
          </div>
          <div class="on-test-range">
            <range-slider
                class="slider"
                min="0"
                :max="$store.state.test_one.length"
                step="1"
                v-model="sliderValue">
            </range-slider>
          </div>
          <div class="on-test-card">
            <div>
              <div>
                <div class="on-test-quiz">
                  {{ $store.state.test_one[sliderValue - 1].title }}
                </div>
                <div class="answers quiz_two">
                 <div>
                   <button class="chois_btn_true" :class="{'active':quiz_active}" @click="getQuiz(true)">
                     {{$t('true')}}
                   </button>
                   <button class="chois_btn_false" :class="{'active':!quiz_active}" @click="getQuiz(false)">
                     {{$t('false')}}
                   </button>
                 </div>

                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import RangeSlider from 'vue-range-slider'
// you probably need to import built-in style
import 'vue-range-slider/dist/vue-range-slider.css'

export default {
    components: {Footer, Header, RangeSlider},
  data() {
    return {
      sliderValue: 1,
      value: 0,
      activeIndex: 0,
      max: 12,
      isActive: false,
      userResponses: [],
      answer: [],
      activeAn:[],
      an_res:{},
      is_response:false,
      timer:80,
      quiz_active:'',
      allAnswer:[]
    }
  },
  mounted() {
    this.$store.dispatch('getTestsId',this.$route.params.id)
    this.$store.dispatch('getQuizOne', this.$route.params.id)
    this.max = this.$store.state.test_one.length
    var k=setInterval(()=>{
        this.timer=this.timer-1
      if(this.timer==0){
        clearInterval(k);
        this.endTest()
      }
    },5000)
  },
  methods: {
    getQuiz(item){
      console.log(item)
      if(item==true){
        this.quiz_active=true
      }
      else{
        this.quiz_active=false
      }
      this.sliderValue=this.sliderValue+1
      this.answer.push({
        "question_id":this.$store.state.test_one[this.sliderValue - 1].id,
        "choice":true
      })
      if((this.sliderValue)==this.$store.state.test_one.length){
        this.endTest()
      }
    },
    getAns(item) {
      this.answer[this.sliderValue - 1] = {
        "question_id": this.$store.state.test_one[this.sliderValue - 1].id,
        "choice_id": item.id
      }

    },
    nextStart() {
      if (this.sliderValue < this.$store.state.test_one.length) {
        this.sliderValue++
      }
    },
    endTest() {
      this.$axiosDefault.post(`test-result/`,
          {
            "answers": this.answer,
            "test_id": this.$route.params.id
          }
          ,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            }
          })
          .then(res=>{
            this.an_res=res.data.result
            this.is_response=true
            this.timer=0
          })
    },
    getOnetest(){
      this.is_response=false
      this.sliderValue=1
      this.answer=[]

    },
    prevStart() {
      if (this.sliderValue > 1) {
        this.sliderValue--
      }
    }
  }
}
</script>
